.pager {
    margin: 20px 0;
    display: flex;
    justify-content: center;
    font-size: 14px;

    span {
        cursor: pointer;
        margin: 0 7px;
        color: #3d96db;


        &.disabled {
            cursor: not-allowed;
            color: #ccc;
        }
        &.active {
            cursor: default;
            color: #e63333;
        }
    }

}